.modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(37, 38, 39, 0.90);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
}

.modal {
    overflow-y: auto;
    padding: 2em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }
    &__dialog {
        background: #fff;
        width: 29.25em;
        border-radius: 0.25em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 1.5em;
        display: inline-block;
        border: 1px solid $primaryBorder;
    }
    &__content{
        @include grid100gap(.5em);
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: 2em;
        @include center;
        &:hover{
            svg{
                transform: scale(1.05);
            }
        }
        svg {
            width: 100%;
            height: auto;
        }
    }
    &__top{
        display: flex;
        justify-content: space-between;
    }
    &__title{
        padding-top: .125em;
    }
    &__nav{
        display: flex;
        justify-content: flex-end;
        padding-top: .625em;
        .m-btn{
            margin-left: .5em;
        }
    }
    &__icon{
        position: absolute;
        left: 0;
        width: 2em;
        height: auto;
    }
    &._danger{
        .modal__dialog{
            border-color: $redBorder;
        }
    }
    &._success{
        .modal__dialog{
            border-color: $successBorder;
        }
    }
    &._warning{
        .modal__dialog{
            border-color: $warningBorder;
        }
    }
}

.modal2{
    .modal__content{
        padding-left: 3em;
        position: relative;
    }
}

.notification{
    .modal__dialog{
        box-shadow: 0 .375em .75em 0 rgba(0, 0, 0, 0.10);
    }
    .modal__close{
        width: 1.5em;
        margin-top: .25em;
    }
    &._danger{
        .modal__dialog{
            background: $redSecondary;
        }
    }
    &._success{
        .modal__dialog{
            background: $successSecondary;
        }
    }
    &._warning{
        .modal__dialog{
            background: $warningSecondary;
        }
    }
    &._info{
        .modal__dialog{
            background: $infoSecondary;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            padding-left: .75em;
            padding-right: .75em;
        }
    }
}
