.news{
  padding: 1.5em 0 3.75em;
  &__top{
    display: flex;
    justify-content: space-between;
    margin-top: 3.125em;
    .local-search{
      width: 37.5em;
      &__field{
        height: 2.75em;
        input{
          height: 100%;
          border-width: 2px;
          padding-left: 2.8em;
          &:not(:hover):not(:focus){
            border-color: $primary;
          }
        }
      }
      &__icon{
        width: 2em;
        left: 1em;
      }
    }
  }
  &__filter{
    display: flex;
    align-items: center;
    .fg{
      min-width: 12.5em;
      margin-left: .5em;
    }
    .m-check{
      margin-right: 1em;
    }
  }
  &__items{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
  }
}

.news-card{
  padding-bottom: 1.5em;
  &__img{
    display: block;
    border-radius: 1.5em;
    @include imgCover(66.67%);
  }
  &__info{
    margin-top: .75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .badge{
      background-color: $primarySecondary;
    }
  }
  &__date{
    display: flex;
    align-items: center;
    img{
      width: 1em;
      margin-right: .5em;
    }
  }
  &__text{
    margin-top: .75em;
    @include grid100gap(.75em);
  }
  &__title{
    @include truncate(3);
  }
  &__desc{
    @include truncate(4);
  }
  &__links{
    margin-top: .5em;
    display: flex;
    flex-wrap: wrap;
    .badge{
      margin: .25em .25em 0 0;
    }
  }
}

.news-item{
  padding: 0 0 5em;
  &__top{
    @include grid100gap(1.5em);
  }
  &__badge{
    display: flex;
  }
  .banner-slider{
    margin-top: 1.5em;
    .swiper{
      &-button{
        position: static;
        transform: unset;
        &-next{
          order: 2;
        }
      }
      &-pagination{
        margin: 0 .67em;
        display: block;
      }
    }
    &__nav{
      margin-top: .75em;
      @include center;
    }
  }
  .text{
    margin-top: 2em;
  }
}

.news-collection{
  padding: 5em 0 3.75em;
}


@media screen and (max-width: 992px){
  .news{
    padding: 1.5em 0;
    &__top{
      margin-top: 1.5em;
      display: block;
      .local-search{
        width: 100%;
      }
    }
    &__filter{
      margin-top: 2em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
      .fg{
        margin: 0;
      }
      .m-check{
        margin: 0;
        order: 2;
      }
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .news-item{
    padding-bottom: 3.75em;
    &__top{
      gap: .75em;
    }
    .banner-slider{
      width: calc(100% + 2em);
      margin-left: -1em;
    }
  }

  .news-collection{
    padding: 3.75em 0 1.5em;
    .news__items{
      margin-top: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .news{
    &__filter{
      grid-template-columns: 100%;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }
}
