.error404{
  margin-bottom: -8.75em;
  padding: 2.5em 0 8em;
  flex-grow: 1;
  position: relative;
  overflow: hidden;
  &__bg{
    position: absolute;
    bottom: -11em;
    left: 3.94em;
    width: 53.8em;
  }
  &__inner{
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 34em calc(100% - 40.5em);
    justify-content: space-between;
    gap: 3em;
  }
  &__content{
    text-align: center;
  }
  &__img{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__img2{
    display: block;
    width: 24.625em;
    max-width: 100%;
    margin: 2.69em auto 0;
  }
  &__nav{
    margin-top: 3.5em;
    display: flex;
    justify-content: center;
  }
}


@media screen and (max-width: 992px){
  .error404{
    background-image: unset;
    padding: 1.5em 0;
    margin: 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img2{
      margin-top: 1.5em;
    }
    &__nav{
      margin-top: 3em;
    }
  }
}
