.info{
  padding: 1.5em 0 3.75em;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 18.75em calc(100% - 21.25em);
    gap: 2.5em;
  }
}


.info-menu{
  &__toggle,
  &__top,
  &__title{
    display: none;
  }
  &__items{
    @include grid100gap(1.5em);
  }
  &__item{
    &._current{
      color: $primary;
    }
    &._active{
      .info-menu__show{
        svg{
          transform: rotate(-180deg);
        }
      }
    }
  }
  &__link,
  &__show{
    transition: all .3s;
    cursor: pointer;
    font-weight: 600;
    display: flex;
    align-items: center;
    padding: .5em 0;
    justify-content: space-between;
    border-bottom: 1px solid $primary;
    &:hover{
      color: $primary;
    }
    svg{
      width: 1em;
      height: auto;
    }
  }
  &__hidden{
    padding: 1.5em 0 0 1em;
    display: none;
  }
  &__sublinks{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__sublink{
    font-weight: 500;
    @include lastM(0 0 1em);
  }
}

.info-card{
  &__top{
    padding: 1em 1.5em;
  }
  &__bottom{
    padding: .25em 1.5em 1.5em;
  }
}

@media screen and (max-width: 992px){
  .info{
    padding: 1.5em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
      margin-top: 1.5em;
    }
  }

  .info-menu{
    @include menu;
    z-index: 20;
    &._active{
      transform: translateX(0);
    }
    .close-btn{
      margin-left: auto;
    }
    &__content{
      padding: 1.5em 1em;
    }
    &__hidden{
      padding-left: 2.44em;
    }
    &__title{
      display: block;
      margin-bottom: 1.5em;
    }
    &__toggle{
      display: flex;
      width: 100%;
      margin-bottom: 1.5em;
    }
  }
}
