.events-type{
  &:not(._show-all) .tag:nth-child(8)~.tag{
    display: flex;
  }
  &:not(._show-all) .tag:nth-child(15)~.tag{
    display: none;
  }
}

.event-card{
  position: relative;
  padding-bottom: .75em;
  &__img{
    display: block;
    border-radius: 1.5em;
    @include imgCover(141.5%);
  }
  &__type{
    position: absolute;
    top: .5em;
    left: .5em;
  }
  &__text{
    margin-top: .75em;
  }
  &__title{
    @include truncate(4);
  }
}

.event-item,
.contest-item{
  &__inner{
    display: grid;
    grid-template-columns: 28.75em calc(100% - 28.75em - 2.5em);
    gap: 2.5em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1.5em;
    }
  }
}

.event-item{
  padding: 0 0 5em;
  &__top{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .badge{
      margin-bottom: .75em;
    }
  }
  .course-props__items{
    margin-top: 4em;
    @media screen and (min-width: 993px){
      grid-template-columns: repeat(2,calc(50% - 1.25em));
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 4em;
    .m-btn{
      margin-left: 2em;
    }
  }
  .course-desc__inner{
    margin-top: 3em;
  }
}

.event-docs{
  padding: 5em 0;
  .subjects__docs{
    margin-top: 0;
    padding: 0;
    border: none;
    @media screen and (min-width: 993px){
      grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    }
  }
  &__top{
    margin-bottom: 3em;
  }
}

._section2{
  padding: 5em 0;
}

.event-collection{
  padding: 5em 0 1.5em;
  &__items{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 7.5em / 4));
    gap: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .events-page{
    .news__items{
      margin-top: .875em;
    }
  }

  .event-item{
    padding-bottom: 3.75em;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__img{
      margin-top: 1.5em;
      max-width: 30em;
    }
    .course-props__items{
      margin-top: 3em;
    }
    &__bottom{
      margin-top: 3em;
    }
    .course-desc__inner{
      margin-top: 4em;
    }
  }

  ._section2,
  .event-docs{
    padding: 3.75em 0;
  }

  .event-collection{
    padding: 3.75em 0 1.5em;
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .event-docs{
    &__top{
      margin-bottom: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .event-item{
    &__bottom{
      flex-direction: column;
      .m-btn{
        margin: 3em 0 0;
        width: 100%;
      }
    }
  }

  .event-collection{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
