.contest-page{
  .programs__items{
    margin-top: 2.5em;
  }
  .contest-card{
    display: inline-block;
    width: 100%;
    @include lastM(0 0 2.5em);
  }
  .courses__more{
    margin-top: 0;
  }
}

.contest-card{
  position: relative;
  padding-bottom: .75em;
  &__img{
    display: block;
    border-radius: 1.5em;
    @include imgCover(50%);
  }
}

.contest-item{
  padding-bottom: 3.75em;
  &__nav{
    margin-top: 2.5em;
    display: flex;
  }
}

.contest-progress{
  &__inner{
    position: relative;
  }
  &__items{
    display: flex;
    justify-content: space-between;
  }
  &__line{
    position: absolute;
    height: 1em;
    width: 100%;
    top: 3.5em;
    background-color: $primarySecondary;
    span{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      background-color: $primary;
      border-radius: 1em;
      transition: all .3s;
    }
  }
}

.step{
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 2;
  overflow: hidden;
  &._first._active{
    .step__icon{
      border-color: $bg;
      background-color: $primary;
    }
    ._fill{
      fill: $primarySecondary;
    }
    ._stroke{
      stroke: $primarySecondary;
    }
  }
  &:not(._first)._active{
    .step__icon{
      border-color: $primary;
    }
    ._fill{
      fill: $primary;
    }
    ._stroke{
      stroke: $primary;
    }
  }
  &._active{

    .step__date{
      color: $primary;
    }
  }
  &:first-child{
    .step__icon:before{
      display: block;
    }
  }
  &:last-child{
    .step__icon:before{
      display: block;
      right: unset;
      left: 100%;
      margin: 0 0 0 .1875em;
    }
  }
  &__icon{
    margin-top: .875em;
    width: 2.75em;
    height: 2.75em;
    @include center;
    border-radius: 50%;
    background-color: $bg;
    border: .1875em solid $primaryBorder;
    position: relative;
    &:before{
      display: none;
      content: '';
      position: absolute;
      top: 50%;
      right: 100%;
      transform: translateY(-50%);
      height: 2em;
      width: 20em;
      background-color: $bg;
    }
    svg{
      width: 1.5625em;
      height: auto;
    }
  }
  &__info{
    margin-top: .875em;
  }
}


@media screen and (max-width: 992px){
  .contest-page{
    .programs__items{
      margin-top: .875em;
    }
    .contest-card{
      @include lastM(0 0 1.5em);
    }
  }

  .contest-item{
    padding-bottom: 2.5em;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.5em;
      padding-bottom: 5.625em;
      position: relative;
    }
    &__nav{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
    }
  }

  .contest-progress{
    overflow-x: auto;
    &__inner{
      min-width: 70.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .contest-page{
    .courses__more{
      margin-top: 1.5em;
    }
  }

  .contest-item{
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }
}
