@import "../vars.scss";
.breadcrumbs {
    padding: 2em 0 3em;
    &__items{
        padding: .25em 0;
    }
    &__item {
        font-weight: 500;
        position: relative;
        display: inline;
        span{
            font-size: 1.125em;
            line-height: 145%;
        }
        &:last-child {
            color: $grey60;
            span{
                font-size: 1.25em;
                line-height: 140%;
            }
        }
        &:not(:first-child) {
            &:before {
                content: '/';
                font-size: 1.125em;
                margin: 0 .1em;
            }
        }
    }
    &__mob{
        display: none;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1.5em 0;
        &__items{
            display: none;
        }
        &__mob{
            display: flex;
        }
        &__back{
            display: flex;
            align-items: center;
            img{
                width: 3em;
                flex-shrink: 0;
                margin-right: .625em;
            }
        }
    }
}
