@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_grid.scss";
.swiper-button{
  width: 3em;
  height: auto;
  @include center;
  margin: 0;
  top: 50%;
  transform: translateY(-50%);
  svg{
    width: 100%;
    height: auto;
  }
  &-prev{
    left: -4.38em;
  }
  &-next{
    right: -4.38em;
  }
  &:hover{
    ._fill{
      fill: $primaryHover
    }
    ._stroke{
      stroke: $primaryHover;
    }
  }
  &:active{
    ._fill{
      fill: $primaryPressed
    }
    ._stroke{
      stroke: $primaryPressed;
    }
  }
  &.swiper-button-disabled{
    ._fill{
      fill: $grey50
    }
    ._stroke{
      stroke: $grey50;
    }
  }
}

.swiper-pagination{
  position: static;
  width: auto;
  font-weight: 500;
  font-size: 1.125em;
  line-height: 145%;
  color: $grey60;
  &-current{
    color: $grey100;
  }
}

.m-card{
  transition: all .3s;
  position: relative;
  border: 1px solid transparent;
  border-radius: 1.5em;
  &:hover{
    border-color: $primaryBorder;
    .card-nav{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &:focus{
    border-color: $primaryBorder;
    box-shadow: $focusShadow2;
  }
}

.card-nav{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
  background-color: #fff;
  padding: 1.5em 1em 1em;
  z-index: 2;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  .m-btn{
    @include lastM(0 0 .25em);
  }
}

.course-card{
  .card-nav{
    flex-direction: unset;
    justify-content: flex-end;
    padding: 2em 1.5em 1.5em;
    .m-btn{
      @include lastM(0 1em 0 0);
    }
  }
}

.news-card{
  &:hover{
    border-color: transparent;
  }
  .card-nav{
    .news-card__links{
      margin: 0 0 .5em;
    }
  }
}

.program-card2{
  .card-nav{
    padding: 1.5em;
    .program-card2__links{
      margin: -1em 0 .5em;
    }
  }
}

.event-card{
  overflow: hidden;
  .card-nav{
    padding-top: 2em;
  }
}


@media screen and (max-width: 992px){
  .card-nav{
    display: none;
  }
}
