.course{
  padding-bottom: 3.75em;
  &__inner{
    position: relative;
    padding-top: 2.25em;
    padding-right: 7.25em;
    display: grid;
    grid-template-columns: calc(100% - 21.5em - 6.25em) 21.5em;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 7.25em;
    width: 21.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__nav{
    margin-top: 4em;
    display: flex;
    .m-btn{
      @include lastM(0 1em 0 0);
    }
  }
}

.course-props{
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.875em));
    gap: 2.5em;
  }
  &__title{
    padding: .5em 0;
    border-bottom: 1px solid $grey40;
  }
  &__desc{
    display: flex;
    align-items: center;
    margin-top: .75em;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

.course-desc{
  &__inner{
    display: grid;
    grid-template-columns: 37.5em calc(100% - 43.75em);
    gap: 3em 6.25em;
  }
  &__group{
    @include lastM(0 0 4em);
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: .75em;
    .tag{
      margin: .75em .75em 0 0;
    }
  }
  .organizer-card{
    margin-bottom: 1em;
  }
}

.teachers{
  padding-top: 6.25em;
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 7.5em;
  }
}

.teacher-slider{
  margin-top: 3em;
  @media screen and (min-width: 993px){
    margin-bottom: -5em;
  }
  .swiper{
    &-slide{
      width: 25%;
    }
    &-button{
      top: 4.44em;
      transform: unset;
    }
  }
}

.teacher-card{
  border-radius: .25em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &:hover{
    color: $black;
    background-color: $grey20;
  }
  @media screen and (min-width: 993px){
    min-height: 17.125em;
    &:not(:hover){
      .text16{
        @include truncate(1);
      }
    }
  }

  &:focus{
    box-shadow: $focusShadow      ;
  }
  .avatar{
    width: 7.5em;
    height: 7.5em;
  }
  &__content{
    margin-top: 1em;
    @include grid100gap(.5em);
  }
}

.course-program{
  &__items{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.37em 2.5em;
  }
}

.program-card{
  min-height: 10em;
  padding: 1.5em;
  display: flex;
  border-radius: 1.5em;
  background-color: $grey20;
  &__num{
    flex-shrink: 0;
    @include sameNumWidth;
    margin-right: 2em;
  }
}

.organizer{
  padding: 5em 0 3.75em;
  &__grid{
    display: grid;
    grid-template-columns: 35em calc(100% - 41.25em);
    gap: 6.25em;
    margin-top: 3em;
  }
  &__info{
    margin-top: 2.75em;
    @include grid100gap(1.5em);
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
}

.organizer-card{
  padding: 1.5em;
  border-radius: 1.5em;
  background-color: $grey20;
  &__img{
    width: 13.8em;
    display: block;
    margin-bottom: 1.5em;
  }
}

.reviews{
  &__top,
  &__desc{
    display: flex;
    align-items: center;
  }
  &__desc{
    margin-left: .75em;
    img{
      width: 2em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

.reviews-slider{
  margin-top: 3em;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}

.review-card{
  display: flex;
  flex-direction: column;
  padding: 1.5em;
  border-radius: 1.5em;
  background-color: $grey20;
  height: 100%;
  &__text{
    margin-top: .75em;
  }
  &__author{
    padding-top: 1.375em;
    margin-top: auto;
    display: flex;
    align-items: flex-start;
    &-info{
      @include grid100gap(.25em);
    }
    .avatar{
      width: 3.75em;
      height: 3.75em;
      margin-right: .75em;
    }
  }
}


@media screen and (max-width: 992px){
  .course{
    padding-bottom: 2.5em;
    &__inner{
      grid-template-columns: 100%;
      padding: 0;
    }
    &__img{
      position: static;
      margin: 1.5em 0;
      width: 100%;
      max-width: 30em;
    }
    .course-card__code{
      margin-bottom: .75em;
    }
    &__nav{
      margin-top: 3em;
    }
  }

  .course-props {
    &__items {
      grid-template-columns: repeat(2, calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .course-desc{
    &__inner{
      grid-template-columns: 100%;
    }
    &__group{
      @include lastM(0 0 2em);
    }
  }

  .teachers{
    padding: 6em 0 3.75em;
    overflow: hidden;
    &__nav{
      margin-top: 4em;
    }
  }

  .teacher-slider{
    margin-top: 2em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 14.5em;
        padding-right: 2em;
      }
    }
  }

  .course-program{
    padding: 3.75em 0;
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
  }

  .organizer {
    padding: 3.75em 0 6em;
    &__grid {
      grid-template-columns: 100%;
      margin-top: 2em;
    }
    &__img{
      display: none;
    }
    &__info{
      margin-top: 2em;
    }
  }

  .reviews{
    overflow: hidden;
    &__top{
      display: block;
    }
    &__desc{
      margin: 0;
    }
  }

  .reviews-slider{
    margin-top: 2em;
    .swiper{
      &-slide{
        padding-right: 1.5em;
        width: 18.625em;
      }
    }
  }

  .review-card{
    &__text{
      .text20{
        @include truncate(9);
      }
    }
  }
}


@media screen and (max-width: 576px){
  .course{
    &__nav{
      display: block;
      .m-btn{
        width: 100%;
        @include lastM(0 0 2em 0);
      }
    }
  }

  .course-props {
    &__items {
      grid-template-columns: 100%;
      text-align: center;
      .course-props__desc{
        justify-content: center;
      }
    }

  }

  .teachers{
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .course-program{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .program-card{
    min-height: unset;
  }
}
