.expertise{
  padding: 1.5em 0 3.75em;

  &__tabs{
    margin-top: 3em;
    @media screen and (min-width: 993px){
      .tabs__dropdown{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
        gap: 2.5em;
      }
      .expertise__tab{
        min-height: 10em;
        @include center;
        border-radius: 1.5em;
        background-color: $grey20;
        padding: 1.5em;
        text-align: center;
        color: $grey90;
        transition: all .3s;
        cursor: pointer;
        position: relative;
        &:before{
          content: '';
          width: 2.0625em;
          height: 1.625em;
          position: absolute;
          left: 50%;
          top: 100%;
          transform: translate(-50%,-.5625em);
          background-image: url(../img/triangle.svg);
          @include bgImgSize(100%);
          opacity: 0;
          transition: inherit;
        }
        &:hover,
        &._active{
          background-color: $primary;
          color: #fff;
        }
        &._active{
          pointer-events: none;
          &:before{
            opacity: 1;
          }
        }
      }
    }
  }
  .tab-content{
    margin-top: 3em;
  }
  &__items{
    @include grid100gap(1.5em);
  }
}

.tabs{
  &__toggle{
    display: none;
  }
  &__dropdown{
    display: flex;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.qa-card,
.info-card{
  border-radius: 1em;
  background-color: $grey20;
  border: 1px solid $grey20;
  transition: all .3s;
  .text{
    color: $grey80;
  }
  &__docs{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    align-items: flex-start;
  }
  .doc-card{
    background-color: #fff;
  }
}

.qa-card{
  &:hover{
    background-color: $primarySecondary;
    border-color: $primarySecondary;
  }
  &:focus{
    border-color: $primaryFocus;
    box-shadow: $focusShadow2;
  }
  &._active{
    .qa-card__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__show{
    cursor: pointer;
    padding: 1em 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: 1em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    padding: .25em 1.5em 1.5em;
    display: none;
  }
}


@media screen and (max-width: 992px){
  .expertise{
    padding: 1.5em 0;
    .tab-content,
    &__tabs{
      margin-top: 1.5em;
    }
  }

  .tabs{
    position: relative;
    z-index: 3;
    padding: 0 .75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 1.5em;
    border: 1px solid $grey40;
    height: 2.75em;
    width: 100%;

    &:hover{
      border-color: $primary;
    }
    &:focus {
      border-color: $primaryFocus;
      box-shadow: 0 0 0 2px $primaryFocus;
    }
    &__toggle{
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      &:before{
        order: 3;
        content: '';
        flex-shrink: 0;
        width: 1em;
        height: 1em;
        background-image: url(../img/arrow-down.svg);
        @include bgImgSize(100%);
        transition: all .4s;
        margin-left: .25em;
        position: relative;
        z-index: 4;
      }
      &:after{
        opacity: 0;
        transform: unset !important;
        content: '';
        position: absolute;
        left: 1px;
        top: 1px;
        height: calc(100% - .25em);
        width: calc(100% - 2px);
        background-color: $bg;
        border-radius: 1.5em 1.5em 0 0;
      }
      span{
        position: relative;
        z-index: 2;
        font-size: 1em;
      }
    }
    &__current{
      position: relative;
      z-index: 3;
      @include truncate(1);
    }
    &__dropdown{
      display: block;
      top: 0;
      margin: 0;
      width: 100%;
      border-radius: 1.5em;
      padding-top: 2.75em;
      background-color: #fff;
      border: 1px solid $grey40;
      z-index: 2;
      max-height: 20em;
      overflow-y: auto;
      position: absolute;
      left: 0;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      ._tab{
        display: flex;
        padding: .38em 1em;
        font-weight: 500;
        &._active{
          background-color: $primarySecondary !important;
          color: $primary;
        }
      }
    }

    &._open{
      border: none;
      .tabs__toggle{

        box-shadow: none;
        &:before{
          transform: rotate(-180deg);
        }
        &:after{
          opacity: 1;
        }
      }
      .tabs__dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .qa-card,
  .info-card{
    &__docs{
      grid-template-columns: 100%;
    }
  }
}
