.subjects{
  padding: 1.5em 0;
  .news__top{
    margin-top: 3em;
  }
  &__docs{
    margin-top: 3em;
    padding: 2em 0;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
    border-top: 1px solid $grey40;
    border-bottom: 1px solid $grey40;
  }
  .sort-field{
    margin-top: 3.125em;
  }
  .programs-collection__items{
    margin-top: .75em;
    align-items: unset;
  }
}

.doc-card{
  padding: 1.5em;
  border-radius: 1.5em;
  background-color: $grey20;
  display: flex;
  align-items: center;
  &__icon{
    width: 3.25em;
    height: 3.25em;
    flex-shrink: 0;
    background-color: $primaryBorder;
    border-radius: 0.75em;
    margin-right: 2em;
    @include center;
    img{
      width: 2em;
    }
  }
}

.subject-item{
  padding-bottom: 5em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 12.5em;
    flex-shrink: 0;
    margin-right: 2.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  .course-props__items{
    margin-top: 2.5em;
    @media screen and (min-width: 993px){
      grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    }
  }
  .text{
    margin-top: 2.5em;
  }
}

.licenses,
.organization-courses{
  padding: 5em 0;
  overflow: hidden;
}

.licenses-slider{
  margin-top: 3em;
  .slider__wrap{
    overflow: unset;
  }
  .swiper{
    &-slide{
      width: 31.25em;
      padding-right: 2.5em;
    }
  }
  .swiper{
    &-button{
      position: static;
      transform: unset;
      &-next{
        order: 2;
      }
    }
    &-pagination{
      margin: 0 .67em;
      display: block;
    }
  }
  &__nav{
    margin-top: .75em;
    @include center;
  }
}

.license-card{
  display: block;
  &__img{
    border-radius: 1.5em;
    @include imgCover(141%);
  }
}

.programs2{
  padding: 5em 0 3.75em;
}


@media screen and (max-width: 992px){
  .subjects{
    &__docs{
      margin-top: 1.5em;
      padding: 1.5em 0;
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
    .sort-field,
    .news__top{
      margin-top: 2em;
    }

    .programs-collection__items{
      margin-top: 3em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .doc-card{
    align-items: flex-start;
  }

  .subject-item{
    padding-bottom: 3.75em;
    &__img{
      margin-right: 1em;
    }
  }

  .licenses,
  .organization-courses{
    padding: 3.75em 0;
  }

  .licenses-slider {
    margin-top: 2em;
    .swiper{
      &-slide{
        width: 20.25em;
        padding-right: 1.5em;
      }
    }
    &__nav{
      display: none;
    }
  }

  .programs2{
    padding: 3.75em 0 1.5em;
    .programs__items{
      margin-top: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .subjects{
    &__docs{
      grid-template-columns: 100%;
    }
    .programs-collection__items{
      grid-template-columns: 100%;
    }
  }

  .subject-item{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__img{
      margin: 1em auto 0;
      order: 2;
    }
    .text,
    .course-props__items{
      margin-top: 4em;
    }
  }
}
