.programs{
  padding: 1.5em 0 3.75em;
  &__items{
    margin-top: 3em;
    column-count: 3;
    column-gap: 2.5em;
    .program-card2{
      display: inline-block;
      width: 100%;
      @include lastM(0 0 2.5em);
    }
  }
  .courses__more{
    margin-top: -.5em;
  }
}

.program-card2{
  padding: 1.5em;
  border-radius: 1.5em;
  border: 1px solid $primaryBorder;
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 3.75em;
    flex-shrink: 0;
    margin-right: 1.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(.75em);
  }
  &__links{
    margin-top: .5em;
    display: flex;
    flex-wrap: wrap;
    .badge{
      margin: 1em 1em 0 0;
    }
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
    justify-content: center;
  }
}

.program-item{
  padding-bottom: 3.75em;
  &__nav{
    display: flex;
    margin-top: 3em;
    .m-btn{
      margin-right: 1em;
    }
  }
  .course-props__items{
    margin-top: 4em;
    ._big{
      grid-column: 2/5;
    }
  }
}

.recommendation,
.programs-collection{
  padding: 3.75em 0;
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
  }
}

.recommendation{
  &__items{
    margin-top: 2em;
  }
}

.recommend-card{
  padding: 1.5em;
  border-radius: 1.5em;
  background-color: $grey20;
  &__top{
    display: flex;
    align-items: center;
    padding-bottom: .5em;
    color: $successMain;
    border-bottom: 1px solid $successHover;
    img{
      margin-right: 1em;
      width: 2em;
      flex-shrink: 0;
    }
    &.red-text{
      border-color: $redHover;
      color: $redMain;
    }
  }
  &__info{
    padding: 1em 0 0 2.5em;
  }
}

.programs-collection{
  &__items{
    margin-top: 3em;
    align-items: flex-start;
  }
}


@media screen and (max-width: 992px){
  .programs{
    padding: 1.5em 0;
    &__items{
      column-count: 2;
      column-gap: 1.5em;
      .program-card2{
        @include lastM(0 0 1.5em);
      }
    }
  }

  .program-item{
    .course-card__code{
      margin-bottom: .75em;
    }
    .course-props__items{
      ._big{
        grid-column: unset;
      }
    }
  }

  .recommendation{
    padding-bottom: 2.75em;
    &__items{
      display: flex;
      width: calc(100% + 2em);
      margin-left: -1em;
      padding: 0 1em 1em;
      overflow-x: auto;
      gap: unset;
    }
    .recommend-card{
      flex-shrink: 0;
      width: 18.75em;
      margin-right: .5em;
    }
  }

  .programs-collection{
    padding-bottom: 1.5em;
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .programs{
    &__items{
      column-count: 1;
    }
    .courses__more{
      margin-top: 1.5em;
    }
  }

  .program-item{
    &__nav{
      @include grid100gap(2em);
      .m-btn{
        width: 100%;
        margin: 0;
      }
    }
  }

  .programs-collection{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
