.admin-page{
  font-size: calc(100vw * (16 / 1920));
  display: grid;
  grid-template-columns: 17.5em calc(100% - 17.5em);
  background: #FAFBFC;
  &__content{
    padding: 9.5em 3em 2em 2em;
    @include grid100gap(4em);
    position: relative;
    z-index: 5;
  }
}

.sidebar{
  background: #FFF;
  height: $vh;
  padding: 2.06em 0;
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 0;
  &__logo{
    display: block;
    width: 8.4375em;
    margin: 0 1.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links{
    margin-top: 3.53em;
  }
  &__link{
    display: flex;
    align-items: center;
    padding: .81em;
    min-height: 4em;
    font-weight: 500;
    color: $grey90;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      opacity: 0;
      transition: inherit;
    }
    &:before{
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(270deg, rgba(113, 102, 249, 0.07) 0%, rgba(113, 102, 249, 0.03) 97.77%);
    }
    &:after{
      z-index: 2;
      height: 100%;
      width: .25em;
      left: 100%;
      top: 0;
      border-radius: 0.25em 0 0 0.25em;
      background: #7166F9;
    }
    span,
    svg{
      position: relative;
      z-index: 2;
    }
    svg{
      width: 2em;
      height: auto;
      flex-shrink: 0;
      margin-right: .625em;
    }
    &:hover,
    &._active{
      color: $black;
      &:before,
      &:after{
        opacity: .4;
      }
      ._stroke{
        stroke: #7166F9;
      }
      .fill{
        fill: #7166F9;
      }
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
  }
  &__logout{
    margin-top: 4em;
  }
}

.admin-header{
  z-index: 10;
  position: fixed;
  top: 0;
  right: 0;
  width: calc(100% - 17.5em);
  height: 7.5em;
  background-color: #fff;
  padding: 0 3em 0 2em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.admin-action{
  display: flex;
  align-items: center;
  &__notification{
    width: 3em;
    height: 3em;
    @include center;
    border-radius: .5em;
    background: #FFFAF1;
    position: relative;
    svg{
      width: 1.75em;
      height: auto;
    }
    span{
      position: absolute;
      top: .31em;
      right: .4em;
      width: .42em;
      height: .42em;
      border-radius: 50%;
      background-color: #EB5757;
    }
  }
}

.admin-user{
  display: flex;
  align-items: center;
  margin-left: 1.5em;
  .avatar{
    width: 3.75em;
    height: 3.75em;
    margin-right: 1.5em;
    img{
      border-radius: 1em;
    }
  }
  &__top{
    min-width: 8.94em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    svg{
      width: 1em;
      height: auto;
      margin-left: 1em;
    }
  }
}

.admin-title{
  padding: .625em 0;
}

.edu{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 43.875em) 41.875em;
    gap: 2em;
    margin-top: 2em;
  }
}

.admin-card{
  border-radius: 1.25em;
  border: 1px solid #F8F9FA;
  background: #FFF;
  box-shadow: 0 .25em 1.25em 0 rgba(238, 238, 238, 0.50);
  padding: 2em;
  @include lastM(0 0 2em);
}

.budget{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1em;
    margin-top: 1.5em;
  }
}

._rect-icon{
  @include center;
  width: 2.25em;
  height: 2.25em;
  border-radius: 0.375em;
  flex-shrink: 0;
  img{
    width: 1.25em;
  }
}

.budget-card{
  border-radius: 1em;
  background: #DCFCE7;
  padding: 2em;
  &._orange{
    background: #FFF4DE;
    .budget-card__currency{
      background: #FF947A;
    }
  }
  &._purple{
    background: #F3E8FF;
    .budget-card__currency{
      background: #BF83FF;
    }
  }
  &__currency{
    background: #3CD856;
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(.25em);
  }
  .text42{
    white-space: nowrap;
  }
}

.done-table{
  margin-top: .75em;
  &__head{
    height: 2.5em;
    color: #96A5B8;
  }
  &__grid{
    display: grid;
    align-items: center;
    grid-template-columns: calc(100% - 29.3875em) 16.625em 12.7625em;
    border-bottom: 1px solid #EDF2F6;
  }
  &__col{
    padding: 0 .625em;
    &:nth-child(3){
      padding: 0;
    }
  }
}

.done-row{
  height: 3.375em;
  margin-top: .75em;
  &__count{
    display: inline-flex;
    align-items: center;
    padding: 0 .75em;
    border-radius: 0.5em;
    border: 1px solid #4AB58E;
    color: #4AB58E;
    background: #E2FFF3;
    height: 2.1875em;
    min-width: 2.1875em;
    &._red{
      color: #FA5A7D;
      border-color: #FA5A7D;
      background: #FFE2E5;
    }
    &._orange{
      color: #FF947A;
      border-color: #FF947A;
      background: #FFF4DE;
    }
  }
}

.done-select{
  &__show{
    color: #0F3659;
    display: flex;
    align-items: center;
    border-radius: 0.5em;
    border: 1px solid #C3D3E2;
    height: 2.75em;
    padding: 0 .75em;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
}

.stat-card{
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__download{
    border-radius: 0.5em;
    border: 1px solid #C3D3E2;
    padding: 0 .75em;
    height: 2.75em;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    color: #0F3659;
    &:hover{
      border-color: #884DFF;
      color: #884DFF;
    }
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__bottom{
    margin-top: 1.12em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
  &__item{
    display: flex;
    align-items: center;
    color: #4AB58E;
    &._blue{
      color: #0095FF;
      .stat-card__icon{
        background: #CDE7FF;
      }
    }
  }
  &__icon{
    background: #E2FFF3;
    margin-right: .75em;
  }
  &__count{
    margin-left: auto;
    padding-left: .5em;
  }
}
