.staff{
  &__grid{
    display: grid;
    grid-template-columns: 40.75em calc(100% - 42.75em);
    gap: 2em;
    margin-top: 2em;
  }
}

.teacher-stat{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    gap: 1.5em;
    margin-top: 2em;
  }
}

.progress-card{
  padding: 1.875em 1.875em 1.11em;
  border-radius: 1.5em;
  border: 1px solid #CCF8FE;
  text-align: center;
  color: #000;
  &__chart{
    background-color: #CCF8FE;
    margin: 1.61em auto 0;
    width: 7em;
    height: 7em;
    border-radius: 50%;
    position: relative;
  }
  &__num{
    @include centerAbsolute;
  }
  &._red{
    .progress-card{
      border-color: #FFE5D3;
      &__chart{
        background-color: #FFE5D3;
      }
    }
    .circle-chart__fill{
      stroke: #FF3A29;
    }
  }
  &._blue{
    .progress-card{
      border-color: #DAD7FE;
      &__chart{
        background-color: #DAD7FE;
      }
    }
    .circle-chart__fill{
      stroke: #4339F2;
    }
  }
  &._green{
    .progress-card{
      border-color: #B5E89F;
      &__chart{
        background-color: #E2FBD7;
      }
    }
    .circle-chart__fill{
      stroke: #34B53A;
    }
  }
}

.circle-chart{
  transform: rotate(-90deg);
  width: 100%;
  height: auto;
}

.salary{
  &__items{
    margin-top: 1.75em;
    @include grid100gap(1.75em);
  }
}

.fg._purple{
  .nice-select{
    border-radius: .5em;
    &:not(.open):focus{
      box-shadow: 0 0 0 2px rgba(#884DFF,.4);
      border-color: rgba(#884DFF,.4);
    }
    &:before{
      background-image: url(../img/select-arrow.svg);
    }
    .list{
      border-radius: .5em;
    }
    .option{
      &.selected{
        color: #884DFF !important;
        background-color: #dad7fe;
      }
    }
  }
}

.salary-stat{
  .fg{
    display: inline-block;
  }
  &__bottom{
    margin-top: .5em;
    position: relative;
  }
  &__num{
    position: absolute;
    right: 0;
    bottom: 100%;
    margin-bottom: .13em;
    color: #FFB200;
    .text18{
      line-height: 144%;
    }
  }
  &._blue{
    .salary-stat__num{
      color: #4339F2;
    }
    .progress-bar{
      background-color: #DAD7FE;
      span{
        background-color: #4339F2;
      }
    }
  }
  &._blue2{
    .salary-stat__num{
      color: #02A0FC;
    }
    .progress-bar{
      background-color: #CCF8FE;
      span{
        background-color: #02A0FC;
      }
    }
  }
  &._red{
    .salary-stat__num{
      color: #FF3A29;
    }
    .progress-bar{
      background-color: #FFE5D3;
      span{
        background-color: #FF3A29;
      }
    }
  }
}

.progress-bar{
  background-color: #FFF5CC;
  height: .5em;
  border-radius: .5em;
  position: relative;
  span{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    border-radius: inherit;
    background-color: #FFB200;
  }
}

.resources{
  padding-bottom: 0;
  &__chart{
    margin-left: -1.5em;
  }
}

.contingent{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 43.875em) 41.875em;
    gap: 2em;
    margin-top: 2em;
  }
}

.students-stat{
  height: 100%;
  padding-bottom: 0;
  &__chart{
    margin: 1em 0 0 -1.5em;
  }
}
