.text{
  p,
  li{
    font-size: 1.25em;
    line-height: 140%;
    @include lastM(0 0 .75em);
  }
  ul{
    @include lastM(0 0 1em);
    padding-left: 1.75em;
    li{
      padding-left: 1em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: .59em;
        left: 0;
        width: .33em;
        height: .33em;
        background-color: $primary;
        border-radius: 50%;
      }
    }
  }
  ol{
    padding-left: 1.5em;
    counter-reset: nums;
    @include lastM(0 0 1em);
    li{
      display: flex;
      counter-increment: nums;
      &:before{
        content: counter(nums) '. ';
        padding-right: .5em;
        @include sameNumWidth;
      }
    }
  }
  b{
    font-weight: 600;
  }
  a{
    color: $primary;
  }
}
